:root {
  --color-white: #ffffff;
  --color-violet: #4c328c;
  --color-pink: #dc4c94;
  --color-blue: #004d9e;
  --main-font: "Nouvel R", "Arial", sans-serif; // шрифт по умолчанию
  --main-color: #081533; // цвет по умолчанию
  --main-font-size: 16px; // размер шрифта по умолчанию
  --color-grey: #fff7fc;
  --color-red: #db282d;
}
a {
  text-decoration: none !important;
  color: inherit;
}

.hidden {
  display: none;
}

.light {
  color: var(--color-grey);
}
.red {
  color: var(--color-red);
}
.white {
  color: var(--color-white);
}
.main {
  color: var(--color-main);
}
.blue {
  color: var(--color-blue);
}

html {
  background-color: var(--color-white);
}
.wrapper {
  // position: absolute;
  // top: 0;
  // left: 0;
  // right: 0;
  // bottom: 0;
  overflow-x: hidden;
  // overflow-y: scroll;
  // -webkit-overflow-scrolling: touch;
  background: var(--main-color);
  min-height: 100%;
}
body {
  font-family: var(--main-font), sans-serif;
  font-weight: normal;
  font-style: normal;
  font-size: var(--main-font-size);
  color: var(--color-white);
}
img {
  max-width: 100%;
  display: block;
}
h1,
h2,
h3,
h4,
p {
  margin: 0;
}
._container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 50px 20px;
}
.parallax {
  min-height: 100vh;
}
.parallax_body {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.parallax_container {
  text-transform: uppercase;
  position: relative;
  z-index: 2;
}
.parallax_label {
  color: #fbd784;
  display: flex;
  font-size: 18px;
  letter-spacing: 6px;
  margin: 0 0 30px 0;
  align-items: center;
}
.parallax_label::before {
  content: "";
  flex: 0 0 70px;
  margin: 0 30px 0 0;
  background-color: #fbd784;
  height: 1px;
}
.parallax_title {
  font-size: 88px;
}
.parallax_images {
  position: absolute;
  width: 120%;
  height: 120%;
  top: -10%;
  left: -10%;
  z-index: 1;
}
.images-parallax {
}
.images-parallax_item {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0%;
  left: 0%;
}
.images-parallax_human,
.images-parallax_clouds,
.images-parallax_mountains {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
}
.images-parallax_clouds {
  background: url("../../public/clouds.jpg") top / cover no-repeat;
  top: 0;
}
.images-parallax_mountains {
  background: url("../../public/mountains.png") top / cover no-repeat;
  top: 15%;
  z-index: 2;
}
.images-parallax_human {
  background: url("../../public/human.png") top / cover no-repeat;
  top: 35%;
  z-index: 3;
}
@media (max-aspect-ratio: 3/2) {
  .images-parallax_human {
    background-position: 20% top;
  }
}
.content {
  background-color: #0b1d26;
  position: relative;
  z-index: 2;
}
.content::before {
  pointer-events: none;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 300px;
  transform: translate(0px, -100%);
  background: linear-gradient(180deg, rgba(11, 29, 38, 0) 5%, #0b1d26 100%);
}
.content_title {
  font-size: 35px;
  margin: 0 0 30px 0;
}
.content_text {
  font-size: 16px;
  line-height: 150%;
}
.content_text p {
  margin: 0 0 30px 0;
}
@media (max-width: 767px) {
  .parallax_title {
    font-size: 50px;
  }
  .parallax_label {
    font-size: 16px;
    margin: 0 0 15px 0;
  }
}

@media (max-width: 479px) {
  .parallax_title {
    font-size: 36px;
  }
  .parallax_label::before {
    flex: 0 0 50px;
    margin: 0 10px 0 0;
  }
}
